* {
    box-sizing: border-box;
}

html {
    font-size: 100%;
    font-family: sans-serif;
}

body {
    margin: 0;
    padding: 0;
    font-family: top -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
    sans-serif;
    font-size: 1rem;
    line-height: 1.5;
    color: gray;
}

button:disabled {
    opacity: 0.5;
}

main {
    width: 100%;
    height: auto;
}

.btn {
    border: none;
    padding: 0;
    overflow: visible;
    font: inherit;
    cursor: pointer;
}

.auth-page {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.auth-page_bg, .auth-page_overlay {
    position: absolute;
    width: 100%;
    height: 100%;
}

.auth-page_bg {
    background: url(/static/media/main-bg.af25a7be.jpg) no-repeat center;
    background-size: cover;
}

.auth-page_overlay {
    background: #0b0f27;
    opacity: 0.7;
}

.auth-form {
    background: transparent;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    position: relative;
    max-width: 400px;
    padding: 20px;
}

.logo {
    width: 55%;
    text-align: right;
}

.auth-form_form {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding-top: 40px;
}

.auth-form_btn, .auth-form_input {
    width: 100%;
    margin: 5px 0px;
    height: 50px;
    border-radius: 6px;
}

.auth-form_btn {
    background: #9fd533;
    outline-color: #08d51a;
    color: white;
    font-weight: bold;
}

.auth-form_input {
    background: white;
    display: inline-block;
    border: 1px solid white;
    padding: 0px 40px;
    color: gray;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.auth-form.error {
    -webkit-animation: bounce .8s ease-in-out both;
            animation: bounce .8s ease-in-out both;
}

.auth-form.error .auth-form_input {
    border-color: #740000;
}

@-webkit-keyframes bounce {
    0%, 100%, 20%, 50%, 80% {
        -webkit-transform:         translateX(0);
                transform:         translateX(0)
    }
    40% {
        -webkit-transform:         translateX(-30px);
                transform:         translateX(-30px)
    }
    60% {
        -webkit-transform:         translateX(-15px);
                transform:         translateX(-15px)
    }
}

@keyframes bounce {
    0%, 100%, 20%, 50%, 80% {
        -webkit-transform:         translateX(0);
                transform:         translateX(0)
    }
    40% {
        -webkit-transform:         translateX(-30px);
                transform:         translateX(-30px)
    }
    60% {
        -webkit-transform:         translateX(-15px);
                transform:         translateX(-15px)
    }
}

.inner-page {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 60px 30px 100px 30px;
}

.list {
    width: 80%;
    min-height: 500px;
    font-size: 14px;
}

.list_header {
    background: #efefef;
}

.list_header, .list_item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px;
    height: 50px;
}

.list_items {
    list-style: none;
    padding: 0;
    margin: 0px;
}

.list_item {
    border-bottom: 1px solid #efefef;
    cursor: pointer;
    -webkit-transition: all .1s linear;
    transition: all .1s linear;
}

.list_item:hover  {
    background: #f8f7fa;
}

.list_item.active {
    background: #f8f7fa;
}

.list_header .btn {
    color: gray;
    font-size: 14px;
    outline: none;
    background: transparent;
}

.favorite-server .list_item {
    background: white;
    cursor: auto;
    cursor: initial;
}
.top-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 35px 30px;
}

.top-header_logo {
    margin-right: 15px;
    bottom: 5px;
    position: relative;
}

.main-navigation {
    text-align: center;
    display: flex;
    align-items: center;
    height: 100%;
}

.main-navigation a {
    color: gray;
    margin-left: 10px;
    font-weight: bold;
    text-decoration: none;
    display: inline-block;
}

.main-navigation a.active {
    color: black;
}

.top-header_btn-logout {
    background: white;
    border: 1px solid #efefef;
    padding: 15px;
}



